body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.card{
  height:200px;
  width:200px;
  background-color:white;
  border-radius:5%;
  margin:10px;
  padding:10px;
  text-align: center
}

.card img {
  height: 90%;
  margin-left:50%;
  -webkit-transform:translateX(-50%);
          transform:translateX(-50%);
}
.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.header {
  width: 100%;
  height: 375px;
  background-color: gray; 
  color: white;
  text-align: center;
  font-family: 'Bodega Sans Light Old Styles'
  }

